body {
  --kb-vh: 1vh;
  --kb-mq-x-small: 'only screen and (max-width: 480px)';
  --kb-font-family-display: 'Libre Bodoni', serif;
  --kb-font-family-body: 'HelveticaNeue-Light', 'Helvetica Neue Light', 'Helvetica Neue', Helvetica, Arial, 'Lucida Grande', sans-serif;
  --kb-color-brand: #f68b1f;
  --kb-color-font-inverse: white;
  --kb-color-font-default: #1e1e1e;
  --kb-color-font: var(--kb-color-font-inverse);
  --kb-color-background-dark: #1e1e1e;
  --kb-color-background-light: #f2f0f0;
  --kb-color-background: var(--kb-color-background-dark);
  --kb-color-project-background-dark: rgba(0, 0, 0, 0.8);
  --kb-color-project-background-light: rgba(255, 255, 255, 0.8);
  --kb-color-project-background: var(--kb-color-project-background-dark);
  --kb-spacing-xx-small: 0.25rem;
  --kb-spacing-x-small: 0.5rem;
  --kb-spacing-small: 0.75rem;
  --kb-spacing-medium: 1rem;
  --kb-spacing-large: 1.5rem;
  --kb-spacing-x-large: 2rem;
  --kb-spacing-xx-large: 3rem;
  --kb-shadow--focus: 0 0 4px var(--kb-color-brand);
  min-height: calc(var(--kb-vh) * 100);
  margin: 0;
  background-color: var(--kb-color-background);
  color: var(--kb-color-font);
  font-family: var(--kb-font-family-body);
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.75;
}

body.kb-freeze {
  overflow: hidden;
}

.kb-container {
  max-width: 1280px;
  margin: 0 auto;
}

.kb-hero {
  margin-top: calc(var(--kb-hero-height) * -1);
  opacity: 0;
}

.kb-hero--loaded {
  margin-top: 0;
  opacity: 1;
  transition: margin-top 500ms ease 500ms, opacity 500ms ease 750ms;
}

.kb-hero__layout {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 8rem;
}

@media (max-width: 1280px) {
  .kb-hero__layout {
    padding: 6rem;
  }
}

.kb-hero__content {
  flex: 0 0 auto;
}

.kb-hero__description {
  margin-top: 2rem;
  max-width: 27.5rem;
}

.kb-hero__sidebar {
  flex: 0 0 auto;
  padding-top: 6.75rem;
}

.kb-hero__logo {
  display: block;
  width: 10rem;
}

.kb-hero__link {
  display: block;
  position: relative;
  color: var(--kb-color-font);
  text-decoration: none;
}

.kb-hero__link-list {
  list-style: none;
  padding-left: 2.75rem;
  display: flex;
  margin: 1.25rem 0 0;
}

.kb-hero__link a {
  display: block;
  padding: 0.5rem 0.525rem;
  border-radius: 0.25rem;
  color: var(--kb-color-font);
  text-decoration: none;
  line-height: 1;
}

.kb-hero__link a:hover, .kb-hero__link a:focus, .kb-hero__link a:active {
  color: var(--kb-color-brand);
}

.kb-hero__link a:focus, .kb-hero__link a:active {
  outline: none;
  box-shadow: var(--kb-shadow--focus);
}

.kb-hero__link-label {
  display: block;
  font-size: 0.8rem;
  color: var(--kb-color-brand);
  pointer-events: none;
  position: absolute;
  top: calc(100% - 0.125rem);
  left: 50%;
  transform: translateX(-50%) translateY(-0.5rem);
  opacity: 0;
  transition: all 250ms ease;
}

.kb-hero__link:hover .kb-hero__link-label,
.kb-hero__link:focus-within .kb-hero__link-label {
  opacity: 1;
  transform: translateX(-50%);
}

.kb-hero__controls {
  padding: 0 0 0 3rem;
  margin-top: 4.25rem;
  border: none;
}

.kb-gallery {
  margin-bottom: 4rem;
}

.kb-project {
  flex: 0 0 25%;
  overflow: hidden;
}

.kb-project__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.kb-project__link {
  display: block;
  position: relative;
  padding-bottom: 66.666%;
}

.kb-project__thumbnail {
  position: absolute;
  inset: 0;
  z-index: 0;
  width: 100%;
  height: 100%;
  margin-top: 100%;
  opacity: 0;
  transition: all 250ms ease;
}

.kb-project--loaded .kb-project__thumbnail,
.kb-project--revealed .kb-project__thumbnail {
  margin-top: 0;
  opacity: 1;
}

.kb-project--revealed .kb-project__thumbnail {
  transition-property: transform, filter;
}

.kb-project__link:hover .kb-project__thumbnail,
.kb-project__link:focus .kb-project__thumbnail {
  transform: scale(1.05);
  filter: blur(5px) saturate(0.75);
  mix-blend-mode: screen;
}

.kb-project__hover-tile {
  position: absolute;
  inset: 0;
  padding: 15% 35% 15% 15%;
  opacity: 0;
  transition: opacity 250ms ease;
  background-color: var(--kb-color-project-background);
}

.kb-project__link:hover .kb-project__hover-tile,
.kb-project__link:focus .kb-project__hover-tile {
  opacity: 1;
}

.kb-project__name, .kb-project__year {
  display: block;
  position: relative;
  color: var(--kb-color-font);
  transform: translateX(-1rem);
  transition: all 250ms ease 150ms;
  opacity: 0;
}

.kb-project__link:hover .kb-project__name,
.kb-project__link:focus .kb-project__name, .kb-project__link:hover .kb-project__year,
.kb-project__link:focus .kb-project__year {
  transform: none;
  opacity: 1;
}

.kb-project__name {
  font-weight: 700;
  font-size: 1.25rem;
  line-height: 1.25;
  color: var(--kb-color-brand);
  transition-delay: 100ms;
}

.kb-footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 4rem 4rem;
  opacity: 0;
  transition: opacity 500ms ease 750ms;
}

.kb-footer--loaded {
  opacity: 1;
}

.kb-checkbox__input {
  display: block;
  width: 1px;
  height: 1px;
  border: 0;
  clip: rect(0 0 0 0);
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  pointer-events: auto;
}

.kb-checkbox__faux {
  display: block;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 0.25rem;
  border: thin solid rgba(136, 136, 136, 0.5);
  position: relative;
}

.kb-checkbox__input:focus + .kb-checkbox__layout .kb-checkbox__faux,
.kb-checkbox__input:active + .kb-checkbox__layout .kb-checkbox__faux {
  box-shadow: var(--kb-shadow--focus);
}

.kb-checkbox__faux::before {
  display: none;
  content: '';
  height: 0.25rem;
  width: 0.5rem;
  position: absolute;
  top: 45%;
  left: 50%;
  transform-origin: center;
  transform: translate3d(-50%, -50%, 0) rotate(-45deg);
  border-left: 2px solid var(--kb-color-brand);
  border-bottom: 2px solid var(--kb-color-brand);
}

.kb-checkbox__input:checked + .kb-checkbox__layout .kb-checkbox__faux::before {
  display: block;
}

.kb-checkbox__layout {
  display: flex;
}

.kb-checkbox__label {
  display: block;
  line-height: 1;
  margin-left: 0.375rem;
  padding-top: 0.25rem;
}

.kb-project-modal {
  position: fixed;
  inset: 0;
  z-index: 9000;
  opacity: 0;
  transition: opacity 100ms ease;
}

.kb-project-modal--shown {
  opacity: 1;
}

.kb-project-modal__backdrop {
  position: absolute;
  overflow: hidden;
  opacity: 0;
  transition: all 250ms ease, opacity 100ms ease;
}

.kb-project-modal__backdrop::after {
  content: '';
  display: block;
  position: absolute;
  inset: 0;
  background-color: var(--kb-color-project-background);
}

.kb-project-modal__background {
  display: block;
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transform: scale(1.25);
  filter: blur(15px) saturate(0.75);
}

.kb-project-modal__content {
  padding: 4rem;
  position: relative;
  z-index: 1;
  opacity: 0;
  transition: opacity 250ms ease 250ms;
}

.kb-project-modal__content--shown {
  opacity: 1;
}

