                text-align: left;
            }
            
            &--right {
                text-align: right;
            }
        }

        &-heading {
            display: block;
            font-family: var(--kb-font-family-display);
            font-weight: 400;
            line-height: 1.25;
            margin: 1rem 0;
        
            &--small {
                font-size: 1.5rem;
            }
        
            &--medium {
                font-size: 2.5rem;
            }
        
            &--large {
                font-size: 3.5rem;
            }
        }
        
        &-color {
            &--brand {
                color: var(--kb-color-brand);
            }
            
            &--regular {
                color: var(--kb-color-font);
            }
        }
    }

